@font-face {
	font-family: "bk-biz-components";
	src: url("fonts/iconcool.svg#iconcool") format("svg"),
url("fonts/iconcool.ttf") format("truetype"),
url("fonts/iconcool.woff") format("woff"),
url("fonts/iconcool.eot?#iefix") format("embedded-opentype");
    font-weight: normal;
    font-style: normal;
}

.bk-biz-components-icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'bk-biz-components' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  text-align: center;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.bk-biz-icon-close:before {
	content: "\e2d4";
}
.bk-biz-icon-close-circle-shape:before {
	content: "\ee8b";
}
